.mainbox {
    background-color: #BCD8DF;
    height : inherit;
    margin:10%;
    border-width : 20px 5px;
    border-color: darkslategrey; 
    border-radius: 28px;
    padding-bottom: 5%;

}
.inputDate {
    margin-left:30%;
    width : 200px;
    padding-right:5%;
    height:50px;
    color:#127681 ;
    border-radius:25px;
    padding-left:4%;
    border-width:5px;
}

.myButton {
	box-shadow: 3px 3px 0px 0px #899599;
	background:linear-gradient(to bottom, #ededed 5%, #0d5e52 100%);
	background-color:#ededed;
	border-radius:29px;
	border:3px solid #d6bcd6;
	display:inline-block;
	cursor:pointer;
	color:#f2f2f2;
	font-family:Arial;
	font-size:20px;
	padding:12px 29px;
	text-decoration:none;
	text-shadow:0px 1px 0px #e1e2ed;
}
.myButton:hover {
	background:linear-gradient(to bottom, #0d5e52 5%, #ededed 100%);
	background-color:#0d5e52;
}
.myButton:active {
	position:relative;
	top:1px;
}
